import Link from 'next/link';
import Image from 'next/image';
import { getTranslations } from 'next-intl/server';

export default async function MetalsPage() {
  const t = await getTranslations('metals');

  const metals = [
    { symbol: 'AUXG', name: t('gold'), desc: t('goldDesc'), icon: '/metals/gold.png', color: '#E2B857', href: '/metals/auxg' },
    { symbol: 'AUXS', name: t('silver'), desc: t('silverDesc'), icon: '/metals/silver.png', color: '#C0C0C0', href: '/metals/auxs' },
    { symbol: 'AUXPT', name: t('platinum'), desc: t('platinumDesc'), icon: '/metals/platinum.png', color: '#E5E4E2', href: '/metals/auxpt' },
    { symbol: 'AUXPD', name: t('palladium'), desc: t('palladiumDesc'), icon: '/metals/palladium.png', color: '#CED0CE', href: '/metals/auxpd' },
  ];

  return (
    <>
      <section className="mobile-pt" style={{ paddingTop: '160px', paddingBottom: '80px', position: 'relative', overflow: 'hidden' }}>
        <div style={{ position: 'absolute', top: '20%', right: '-5%', width: '400px', height: '400px', background: 'radial-gradient(circle, rgba(226,184,87,0.08) 0%, transparent 70%)', filter: 'blur(60px)' }}></div>
        
        <div className="mobile-padding" style={{ maxWidth: '1000px', margin: '0 auto', padding: '0 24px' }}>
          <div style={{ textAlign: 'center', maxWidth: '600px', margin: '0 auto 64px auto' }}>
            <div style={{ display: 'inline-flex', alignItems: 'center', gap: '8px', padding: '8px 16px', borderRadius: '100px', background: 'rgba(226,184,87,0.08)', border: '1px solid rgba(226,184,87,0.2)', marginBottom: '24px' }}>
              <span style={{ color: '#E2B857', fontSize: '13px', fontWeight: 500 }}>{t('badge')}</span>
            </div>
            
            <h1 className="section-title" style={{ fontFamily: "'Space Grotesk', sans-serif", fontSize: '48px', fontWeight: 700, color: 'white', marginBottom: '20px', letterSpacing: '-0.02em' }}>
              {t('title')} <span className="text-gold-gradient">{t('titleHighlight')}</span>
            </h1>
            <p style={{ fontSize: '17px', color: '#71717A', lineHeight: 1.7 }}>{t('subtitle')}</p>
          </div>

          {/* Metal Cards */}
          <div className="grid-2" style={{ display: 'grid', gridTemplateColumns: 'repeat(2, 1fr)', gap: '20px', marginBottom: '48px' }}>
            {metals.map((metal) => (
              <Link 
                key={metal.symbol}
                href={metal.href}
                style={{ 
                  background: 'rgba(255,255,255,0.02)', border: '1px solid rgba(255,255,255,0.06)',
                  padding: '32px', borderRadius: '20px', textDecoration: 'none'
                }}
              >
                <div style={{ display: 'flex', alignItems: 'center', gap: '16px', marginBottom: '16px' }}>
                  <Image src={metal.icon} alt={metal.name} width={56} height={56} />
                  <div>
                    <h3 style={{ fontFamily: "'Space Grotesk', sans-serif", fontSize: '24px', fontWeight: 700, color: 'white', margin: 0 }}>{metal.symbol}</h3>
                    <p style={{ color: metal.color, fontSize: '14px', margin: 0 }}>{metal.name}</p>
                  </div>
                </div>
                <p style={{ color: '#71717A', fontSize: '14px', lineHeight: 1.6, margin: 0 }}>{metal.desc}</p>
              </Link>
            ))}
          </div>

          {/* Soft Launch Info */}
          <div style={{ background: 'linear-gradient(135deg, rgba(251,191,36,0.08) 0%, rgba(217,119,6,0.08) 100%)', border: '1px solid rgba(251,191,36,0.2)', padding: '28px', borderRadius: '16px', textAlign: 'center' }}>
            <h3 style={{ fontFamily: "'Space Grotesk', sans-serif", fontSize: '18px', fontWeight: 600, color: '#FBBF24', marginBottom: '12px' }}>{t('softLaunchTitle')}</h3>
            <p style={{ color: '#A1A1AA', fontSize: '14px', margin: 0 }}>{t('softLaunchDesc')}</p>
          </div>
        </div>
      </section>
    </>
  );
}
